.loader-container {
    width: 200px;
    height: 200px;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.loader {
    width: calc(100% - 16px);
    height: calc(100% - 16px);
    /* border: 8px solid #162534; */
    border-top: 8px solid #09f;
    border-radius: 50%;
    animation: rotate 3s linear infinite;
}

@keyframes rotate {
    100% {
        transform: rotate(360deg);
    }
}